.Header-header {
  padding: 0;
  color: #00dd3b;
  overflow: hidden;
  position: relative;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Header-logos {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 40px;
}

.Header-logo {
  height: 80px;
}

.Header-logo + .Header-logo {
  margin-left: 40px;
}

.Header-title {
  font: 100px/1.16 serif;
  font-weight: 900;
  color: #fff;
}

.Header-video {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  top: 0;
  left: 0;
}

.Header-overlay {
  background: url("data:image/svg+xml;base64,") 50% rgba(0, 0, 0, .4);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
}

.Header-wrapper {
  position: relative;
  min-height: 100%;
  z-index: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}



