.Body-body {
  display: -ms-flexbox;
  display: flex;
  padding: 32px;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #1a1d1b;
}

.Body-container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 34px 0 80px;
  color: #fff;
}

.Body-container p {
  font: 20px/1.6 serif;
  font-weight: 200;
  max-width: 960px;
  margin: 18px auto;
}

.Body-container b {
  font-weight: 900;
}

.Body-title {
  font: 68px/1.12 serif;
  font-weight: 900;
  text-transform: uppercase;
  margin: 14px 0 46px;
  background: -webkit-linear-gradient(300deg, #9f0, #00dd3b);
  background: -o-linear-gradient(300deg, #9f0, #00dd3b);
  background: linear-gradient(150deg, #9f0, #00dd3b);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.Body-img {
  width: calc(100% / 3);
  height: 280px;
  margin-bottom: 20px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -o-object-fit: cover;
     object-fit: cover;
}

.Body-images {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Body-title small {
  font: 34px/1.4 serif;
  font-weight: 100;
  margin: 58px 0 14px;
  color: #fff;
}



